"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Dialog, DialogContent, DialogDescription, DialogFooter, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Badge } from "@/components/ui/badge"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { format } from "date-fns"

interface FeeRecord {
  id: string
  studentId: string
  studentName: string
  class: string
  feeType: string
  amount: number
  dueDate: string
  paidDate?: string
  status: "paid" | "pending" | "overdue"
  paymentMethod?: string
  transactionId?: string
}

interface Expense {
  id: string
  category: string
  description: string
  amount: number
  date: string
  approvedBy: string
  status: "approved" | "pending" | "rejected"
}

export default function FinancePage() {
  const [searchTerm, setSearchTerm] = useState("")
  const [isFeeDialogOpen, setIsFeeDialogOpen] = useState(false)
  const [isExpenseDialogOpen, setIsExpenseDialogOpen] = useState(false)
  const [editingFee, setEditingFee] = useState<FeeRecord | null>(null)
  const [editingExpense, setEditingExpense] = useState<Expense | null>(null)

  // Mock data
  const [feeRecords, setFeeRecords] = useState<FeeRecord[]>([
    {
      id: "1",
      studentId: "1",
      studentName: "John Doe",
      class: "10A",
      feeType: "Tuition Fee",
      amount: 1500,
      dueDate: "2024-02-15",
      paidDate: "2024-02-10",
      status: "paid",
      paymentMethod: "Bank Transfer",
      transactionId: "TXN001"
    },
    {
      id: "2",
      studentId: "2",
      studentName: "Jane Smith",
      class: "9B",
      feeType: "Library Fee",
      amount: 200,
      dueDate: "2024-02-20",
      status: "pending"
    },
    {
      id: "3",
      studentId: "3",
      studentName: "Mike Johnson",
      class: "11A",
      feeType: "Lab Fee",
      amount: 300,
      dueDate: "2024-01-30",
      status: "overdue"
    }
  ])

  const [expenses, setExpenses] = useState<Expense[]>([
    {
      id: "1",
      category: "Infrastructure",
      description: "Classroom renovation",
      amount: 5000,
      date: "2024-02-01",
      approvedBy: "Principal",
      status: "approved"
    },
    {
      id: "2",
      category: "Equipment",
      description: "Laboratory equipment purchase",
      amount: 2500,
      date: "2024-02-05",
      approvedBy: "Vice Principal",
      status: "pending"
    }
  ])

  const filteredFeeRecords = feeRecords.filter(record =>
    record.studentName.toLowerCase().includes(searchTerm.toLowerCase()) ||
    record.class.toLowerCase().includes(searchTerm.toLowerCase()) ||
    record.feeType.toLowerCase().includes(searchTerm.toLowerCase())
  )

  const filteredExpenses = expenses.filter(expense =>
    expense.category.toLowerCase().includes(searchTerm.toLowerCase()) ||
    expense.description.toLowerCase().includes(searchTerm.toLowerCase())
  )

  const handleAddFee = () => {
    setEditingFee(null)
    setIsFeeDialogOpen(true)
  }

  const handleEditFee = (fee: FeeRecord) => {
    setEditingFee(fee)
    setIsFeeDialogOpen(true)
  }

  const handleDeleteFee = (id: string) => {
    setFeeRecords(feeRecords.filter(fee => fee.id !== id))
  }

  const handleSaveFee = (formData: FormData) => {
    const feeData = {
      id: editingFee?.id || Date.now().toString(),
      studentId: formData.get("studentId") as string,
      studentName: formData.get("studentName") as string,
      class: formData.get("class") as string,
      feeType: formData.get("feeType") as string,
      amount: parseFloat(formData.get("amount") as string),
      dueDate: formData.get("dueDate") as string,
      paidDate: formData.get("paidDate") as string || undefined,
      status: formData.get("status") as "paid" | "pending" | "overdue",
      paymentMethod: formData.get("paymentMethod") as string || undefined,
      transactionId: formData.get("transactionId") as string || undefined
    }

    if (editingFee) {
      setFeeRecords(feeRecords.map(f => f.id === editingFee.id ? feeData : f))
    } else {
      setFeeRecords([...feeRecords, feeData])
    }

    setIsFeeDialogOpen(false)
    setEditingFee(null)
  }

  const handleAddExpense = () => {
    setEditingExpense(null)
    setIsExpenseDialogOpen(true)
  }

  const handleSaveExpense = (formData: FormData) => {
    const expenseData = {
      id: editingExpense?.id || Date.now().toString(),
      category: formData.get("category") as string,
      description: formData.get("description") as string,
      amount: parseFloat(formData.get("amount") as string),
      date: formData.get("date") as string,
      approvedBy: formData.get("approvedBy") as string,
      status: formData.get("status") as "approved" | "pending" | "rejected"
    }

    if (editingExpense) {
      setExpenses(expenses.map(e => e.id === editingExpense.id ? expenseData : e))
    } else {
      setExpenses([...expenses, expenseData])
    }

    setIsExpenseDialogOpen(false)
    setEditingExpense(null)
  }

  const getFinancialSummary = () => {
    const totalFeesDue = feeRecords.reduce((sum, record) => sum + record.amount, 0)
    const totalFeesCollected = feeRecords
      .filter(record => record.status === "paid")
      .reduce((sum, record) => sum + record.amount, 0)
    const totalExpenses = expenses
      .filter(expense => expense.status === "approved")
      .reduce((sum, expense) => sum + expense.amount, 0)
    const pendingFees = feeRecords.filter(record => record.status === "pending").length
    const overdueFees = feeRecords.filter(record => record.status === "overdue").length

    return {
      totalFeesDue,
      totalFeesCollected,
      totalExpenses,
      pendingFees,
      overdueFees,
      netIncome: totalFeesCollected - totalExpenses
    }
  }

  const summary = getFinancialSummary()

  const getStatusColor = (status: string) => {
    switch (status) {
      case "paid": case "approved": return "default"
      case "pending": return "secondary"
      case "overdue": case "rejected": return "destructive"
      default: return "outline"
    }
  }

  return (
    <div className="flex-1 space-y-6 p-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Finance</h1>
          <p className="text-muted-foreground">
            Manage fees, payments, and expenses
          </p>
        </div>
      </div>

      {/* Financial Summary */}
      <div className="grid gap-4 md:grid-cols-3 lg:grid-cols-6">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Fees Due</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">${summary.totalFeesDue.toLocaleString()}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Fees Collected</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">${summary.totalFeesCollected.toLocaleString()}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Expenses</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-red-600">${summary.totalExpenses.toLocaleString()}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Net Income</CardTitle>
          </CardHeader>
          <CardContent>
            <div className={`text-2xl font-bold ${summary.netIncome >= 0 ? 'text-green-600' : 'text-red-600'}`}>
              ${summary.netIncome.toLocaleString()}
            </div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Pending Fees</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-yellow-600">{summary.pendingFees}</div>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Overdue Fees</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-red-600">{summary.overdueFees}</div>
          </CardContent>
        </Card>
      </div>

      <Tabs defaultValue="fees" className="space-y-4">
        <TabsList>
          <TabsTrigger value="fees">Fee Management</TabsTrigger>
          <TabsTrigger value="expenses">Expenses</TabsTrigger>
        </TabsList>

        <TabsContent value="fees" className="space-y-4">
          <Card>
            <CardHeader>
              <div className="flex items-center justify-between">
                <div>
                  <CardTitle>Fee Records</CardTitle>
                  <CardDescription>
                    Manage student fee payments and collections
                  </CardDescription>
                </div>
                <Button onClick={handleAddFee}>
                  Add Fee Record
                </Button>
              </div>
              <div className="flex items-center space-x-2">
                <Input
                  placeholder="Search fee records..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="max-w-sm"
                />
              </div>
            </CardHeader>
            <CardContent>
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Student</TableHead>
                    <TableHead>Class</TableHead>
                    <TableHead>Fee Type</TableHead>
                    <TableHead>Amount</TableHead>
                    <TableHead>Due Date</TableHead>
                    <TableHead>Paid Date</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead>Payment Method</TableHead>
                    <TableHead>Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {filteredFeeRecords.map((record) => (
                    <TableRow key={record.id}>
                      <TableCell className="font-medium">{record.studentName}</TableCell>
                      <TableCell>{record.class}</TableCell>
                      <TableCell>{record.feeType}</TableCell>
                      <TableCell>${record.amount}</TableCell>
                      <TableCell>{format(new Date(record.dueDate), "MMM d, yyyy")}</TableCell>
                      <TableCell>
                        {record.paidDate ? format(new Date(record.paidDate), "MMM d, yyyy") : "-"}
                      </TableCell>
                      <TableCell>
                        <Badge variant={getStatusColor(record.status)}>
                          {record.status}
                        </Badge>
                      </TableCell>
                      <TableCell>{record.paymentMethod || "-"}</TableCell>
                      <TableCell>
                        <div className="flex space-x-2">
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => handleEditFee(record)}
                          >
                            Edit
                          </Button>
                          <Button
                            variant="destructive"
                            size="sm"
                            onClick={() => handleDeleteFee(record.id)}
                          >
                            Delete
                          </Button>
                        </div>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="expenses" className="space-y-4">
          <Card>
            <CardHeader>
              <div className="flex items-center justify-between">
                <div>
                  <CardTitle>Expense Records</CardTitle>
                  <CardDescription>
                    Track and manage school expenses
                  </CardDescription>
                </div>
                <Button onClick={handleAddExpense}>
                  Add Expense
                </Button>
              </div>
              <div className="flex items-center space-x-2">
                <Input
                  placeholder="Search expenses..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="max-w-sm"
                />
              </div>
            </CardHeader>
            <CardContent>
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Category</TableHead>
                    <TableHead>Description</TableHead>
                    <TableHead>Amount</TableHead>
                    <TableHead>Date</TableHead>
                    <TableHead>Approved By</TableHead>
                    <TableHead>Status</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {filteredExpenses.map((expense) => (
                    <TableRow key={expense.id}>
                      <TableCell className="font-medium">{expense.category}</TableCell>
                      <TableCell>{expense.description}</TableCell>
                      <TableCell>${expense.amount}</TableCell>
                      <TableCell>{format(new Date(expense.date), "MMM d, yyyy")}</TableCell>
                      <TableCell>{expense.approvedBy}</TableCell>
                      <TableCell>
                        <Badge variant={getStatusColor(expense.status)}>
                          {expense.status}
                        </Badge>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      {/* Add/Edit Fee Dialog */}
      <Dialog open={isFeeDialogOpen} onOpenChange={setIsFeeDialogOpen}>
        <DialogContent className="sm:max-w-[500px] max-h-[80vh] overflow-y-auto">
          <form action={handleSaveFee}>
            <DialogHeader>
              <DialogTitle>
                {editingFee ? "Edit Fee Record" : "Add Fee Record"}
              </DialogTitle>
              <DialogDescription>
                {editingFee 
                  ? "Update fee record information below."
                  : "Enter fee details to add a new record."
                }
              </DialogDescription>
            </DialogHeader>
            <div className="grid gap-4 py-4">
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="studentName" className="text-right">
                  Student Name
                </Label>
                <Input
                  id="studentName"
                  name="studentName"
                  defaultValue={editingFee?.studentName || ""}
                  className="col-span-3"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="class" className="text-right">
                  Class
                </Label>
                <Select name="class" defaultValue={editingFee?.class || ""}>
                  <SelectTrigger className="col-span-3">
                    <SelectValue placeholder="Select class" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="9A">9A</SelectItem>
                    <SelectItem value="9B">9B</SelectItem>
                    <SelectItem value="10A">10A</SelectItem>
                    <SelectItem value="10B">10B</SelectItem>
                    <SelectItem value="11A">11A</SelectItem>
                    <SelectItem value="11B">11B</SelectItem>
                    <SelectItem value="12A">12A</SelectItem>
                    <SelectItem value="12B">12B</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="feeType" className="text-right">
                  Fee Type
                </Label>
                <Select name="feeType" defaultValue={editingFee?.feeType || ""}>
                  <SelectTrigger className="col-span-3">
                    <SelectValue placeholder="Select fee type" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Tuition Fee">Tuition Fee</SelectItem>
                    <SelectItem value="Library Fee">Library Fee</SelectItem>
                    <SelectItem value="Lab Fee">Lab Fee</SelectItem>
                    <SelectItem value="Sports Fee">Sports Fee</SelectItem>
                    <SelectItem value="Transport Fee">Transport Fee</SelectItem>
                    <SelectItem value="Exam Fee">Exam Fee</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="amount" className="text-right">
                  Amount ($)
                </Label>
                <Input
                  id="amount"
                  name="amount"
                  type="number"
                  step="0.01"
                  defaultValue={editingFee?.amount || ""}
                  className="col-span-3"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="dueDate" className="text-right">
                  Due Date
                </Label>
                <Input
                  id="dueDate"
                  name="dueDate"
                  type="date"
                  defaultValue={editingFee?.dueDate || ""}
                  className="col-span-3"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="status" className="text-right">
                  Status
                </Label>
                <Select name="status" defaultValue={editingFee?.status || "pending"}>
                  <SelectTrigger className="col-span-3">
                    <SelectValue placeholder="Select status" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="paid">Paid</SelectItem>
                    <SelectItem value="pending">Pending</SelectItem>
                    <SelectItem value="overdue">Overdue</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="paidDate" className="text-right">
                  Paid Date
                </Label>
                <Input
                  id="paidDate"
                  name="paidDate"
                  type="date"
                  defaultValue={editingFee?.paidDate || ""}
                  className="col-span-3"
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="paymentMethod" className="text-right">
                  Payment Method
                </Label>
                <Select name="paymentMethod" defaultValue={editingFee?.paymentMethod || ""}>
                  <SelectTrigger className="col-span-3">
                    <SelectValue placeholder="Select payment method" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Cash">Cash</SelectItem>
                    <SelectItem value="Bank Transfer">Bank Transfer</SelectItem>
                    <SelectItem value="Credit Card">Credit Card</SelectItem>
                    <SelectItem value="Cheque">Cheque</SelectItem>
                    <SelectItem value="Online Payment">Online Payment</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="transactionId" className="text-right">
                  Transaction ID
                </Label>
                <Input
                  id="transactionId"
                  name="transactionId"
                  defaultValue={editingFee?.transactionId || ""}
                  className="col-span-3"
                  placeholder="Optional"
                />
              </div>
              <Input name="studentId" type="hidden" defaultValue={editingFee?.studentId || Date.now().toString()} />
            </div>
            <DialogFooter>
              <Button type="button" variant="outline" onClick={() => setIsFeeDialogOpen(false)}>
                Cancel
              </Button>
              <Button type="submit">
                {editingFee ? "Update Record" : "Add Record"}
              </Button>
            </DialogFooter>
          </form>
        </DialogContent>
      </Dialog>

      {/* Add Expense Dialog */}
      <Dialog open={isExpenseDialogOpen} onOpenChange={setIsExpenseDialogOpen}>
        <DialogContent className="sm:max-w-[500px]">
          <form action={handleSaveExpense}>
            <DialogHeader>
              <DialogTitle>Add New Expense</DialogTitle>
              <DialogDescription>
                Enter expense details to add a new record.
              </DialogDescription>
            </DialogHeader>
            <div className="grid gap-4 py-4">
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="category" className="text-right">
                  Category
                </Label>
                <Select name="category" defaultValue="">
                  <SelectTrigger className="col-span-3">
                    <SelectValue placeholder="Select category" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Infrastructure">Infrastructure</SelectItem>
                    <SelectItem value="Equipment">Equipment</SelectItem>
                    <SelectItem value="Utilities">Utilities</SelectItem>
                    <SelectItem value="Salaries">Salaries</SelectItem>
                    <SelectItem value="Maintenance">Maintenance</SelectItem>
                    <SelectItem value="Supplies">Supplies</SelectItem>
                    <SelectItem value="Other">Other</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="description" className="text-right">
                  Description
                </Label>
                <Input
                  id="description"
                  name="description"
                  className="col-span-3"
                  placeholder="Brief description of expense"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="amount" className="text-right">
                  Amount ($)
                </Label>
                <Input
                  id="amount"
                  name="amount"
                  type="number"
                  step="0.01"
                  className="col-span-3"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="date" className="text-right">
                  Date
                </Label>
                <Input
                  id="date"
                  name="date"
                  type="date"
                  className="col-span-3"
                  required
                />
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="approvedBy" className="text-right">
                  Approved By
                </Label>
                <Select name="approvedBy" defaultValue="">
                  <SelectTrigger className="col-span-3">
                    <SelectValue placeholder="Select approver" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Principal">Principal</SelectItem>
                    <SelectItem value="Vice Principal">Vice Principal</SelectItem>
                    <SelectItem value="Finance Manager">Finance Manager</SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="grid grid-cols-4 items-center gap-4">
                <Label htmlFor="status" className="text-right">
                  Status
                </Label>
                <Select name="status" defaultValue="pending">
                  <SelectTrigger className="col-span-3">
                    <SelectValue placeholder="Select status" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="approved">Approved</SelectItem>
                    <SelectItem value="pending">Pending</SelectItem>
                    <SelectItem value="rejected">Rejected</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>
            <DialogFooter>
              <Button type="button" variant="outline" onClick={() => setIsExpenseDialogOpen(false)}>
                Cancel
              </Button>
              <Button type="submit">
                Add Expense
              </Button>
            </DialogFooter>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  )
}
